.content {
    &__all {
        display: none;
        width: 100%;
        text-align: center;
        font-weight: 400;
        font-size: 14px;
        color: #FF6600;
        opacity: 1;
        background: transparent;
        border: none;
        margin: 0;
        padding: 0;
        outline: none;
        transition: .2s opacity ease-in-out;
        margin-top: 12px;
        &:hover {
            opacity: 0.8;
        }
    }
    margin-top: 85px;
    .container {
        position: relative;
    }
    hr {
        margin: 0 0 0 0;
    }
    &__header {
        position: relative;
    }
    .slider {
        padding: 20px 0;
        &__slide {
            text-align: center;
            a {
                font-weight: 400;
                font-size: 18px;
                opacity: 1;
                color: rgba(23, 23, 23, 0.4);
                transition: opacity .2s ease-in-out;
                &:hover {
                    opacity: .8;
                }
            }
        }
        &__slide.swiper-slide-active {
            a {
                color: #fff;
                background: #FF6600;
                padding: 6px 25px;
                border-radius: 25px;
            }
        }
        &__prev {
            z-index: 10;
            position: absolute;
            top: 15px;
            cursor: pointer;
            right: 15;
            left: 98%;
            width: 13px;
            height: 23px;
        }
        &__next {
            z-index: 10;
            position: absolute;
            top: 0px;
            cursor: pointer;
            width: 53px;
            height: 53px;
            right: 0;
            left: 100%;
        }
    }
    .header__mobile {
        gap: 15px;
        display: none;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    }

    .main {
        margin-top: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        &__line {
            display: none;
        }
        &__title {
            margin: 0;
            font-weight: 700;
            font-size: 20px;
            text-transform: uppercase;
            color: #171717;
        }
        &__btn {
            z-index: 25;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            gap: 10px;
            cursor: pointer;
            border: none;
            background: transparent;
            outline: none;
            font-weight: 400;
            font-size: 20px;
            color: #171717;
            opacity: 1;
            transition: opacity .2s ease-in-out;
            &:hover {
                opacity: .8;
            }
            svg {
                transition: transform .2s ease-in-out;
                width: 14px;
                height: 14px;
            }
        }
        &__btn.sort {
            svg {
                transform: rotate(90deg);
            }
        }
        &__btn.sort__btn.active {
            svg {
                transform: rotate(-90deg);
            }
        }
        &__btn.filter__btn.active {
            svg {
                transform: rotate(-180deg);
            }
        }
    }
    .hidden {
        display: none;
    }

    .sort {
        display: none;
        &__wrap {
            position: relative;
        }
        &__block {
            z-index: 30;
            position: absolute;
            right: -20px;
            top: -20px;
            width: 180px;
            background: #F7F7F7;
            border-radius: 12px;
            padding: 20px;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            gap: 40px;
        }
        &__nav {
            margin: 0;
            padding: 0;
            display: flex;
            flex-direction: column;
            gap: 25px;
        }
        &__link {
            list-style-type: none;
            text-align: right;
            font-size: 15px;
            color: #171717;
            opacity: 1;
            transition: opacity .2s ease-in-out;
            &:hover {
                opacity: 0.8;
            }
        }
    }

   .sort.active {
        display: block;
   }

    &__items {
        margin-top: 100px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        gap: 100px;
    }
    .item {
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 27px;
        &__plus {
            cursor: pointer;
            width: 39px;
            height: 39px;
            position: absolute;
            top: -15px;
            right: -15px;
        }
        &__img {
            width: 100%;
        }
        &__name {
            padding: 0;
            margin: 0;
            font-weight: 400;
            font-size: 15px;
            color: #000000;
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        }
        &__priceBlock {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        &__status {
            font-weight: 400;
            font-size: 11px;
            color: #FF6600;
        }
        &__price {
            padding: 0;
            margin: 0;
            margin-top: 3px;
            font-weight: 400;
            font-size: 15px;
            color: #656464;
        }
        &__info {
            margin-top: 10px;
            font-size: 11px;
            color: #171717;
        }
        &__amount {
            display: flex;
            align-items: center;
            gap: 2px;
            button {
                cursor: pointer;
                font-weight: 400;
                font-size: 21px;
                color: #171717;
                padding: 0;
                margin: 0;
                background: transparent;
                border: none;
                outline: none;
            }
            input {
                padding: 0;
                margin: 0;
                height: auto;
                text-align: center;
                width: 25px;
                font-weight: 400;
                font-size: 15px;
                color: #171717;
                border: none;
                outline: none;
            }
        }
    }

    &__wrap.active {
        display: grid;
        grid-template-columns: 1fr 3fr;
        gap: 50px;
        .content__items {
            grid-template-columns: 1fr 1fr 1fr;
        }
    }
    &__filter {
        margin-top: 100px;
    }
    .filter {
        display: none;
        flex-direction: column;
        gap: 30px;
        &__title {
            margin: 0;
            font-weight: 400;
            font-size: 27px;
            color: #171717;
        }
        &__block {
            display: flex;
            flex-direction: column;
            gap: 25px;
            p {
                margin: 0;
                padding: 0;
            }
        }
        &__blockPrice {
            display: flex;
            gap: 50px;
            align-items: center;
        }
        &__price {
            font-weight: 300;
            font-size: 15px;
            color: #000000;
            border: none;
            background: transparent;
            outline: none;
            border-bottom: 1px solid #171717;
            padding-bottom: 10px;
        }
        &__blockColor {
            display: flex;
            align-items: center;
            gap: 25px;
        }
        .filter__color {
            margin: 0 auto;
            display: none;
        }
        .fake-radio {
            margin: 0 auto;
            position: relative;
             display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            width: 22px;
            height: 22px;
            border-radius: 50%;
        }
        .red {
            background: #FF0202;
        }
        .green {
            background: #02FF0C;
        }
        .purple {
            background: #FA02FF;
        }
        .blue {
            background: #0602DF;
        }
        .black {
            background: #000000;
        }
        .fake-radio::before {
            content: '';
            position: absolute;
            width: 40px;
            height: 40px;
            border: 0.5px solid #656464;
            border-radius: 50%;
            transform: scale(0);
            transition: .2s ease-in;
        }
        &__color:hover+.filter__color::before {
            display: flex;
            transform: scale(1);
        }
        &__color:checked+.fake-radio::before {
            display: flex;
            transform: scale(1);
        }
        &__label {
            position: relative;
            width: 50px;
            i {
                position: absolute;
                text-align: center;
                bottom: -30px;
                left: 0;
                right: 0;
                opacity: 0;
                font-weight: 300;
                font-size: 11px;
                transition: opacity .3s ease-in-out;
                color: rgba(23, 23, 23, 0.4);
            }
        }
        .filter__label:hover i {
            opacity: 1;
        }
    }
    .filter.active {
        display: flex;
    }
}


@media (max-width: 1680px) {
    .content__items {
        gap: 80px;
    }
}

@media (max-width: 1400px) {
    .filter__title {
        font-size: 24px !important;
    }
    .sort__block {
        width: 160px !important;
        gap: 30px !important;
    }
    .sort__nav {
        gap: 20px !important;
    }
    .sort__link {
        font-size: 14px !important;
    }
    .content__wrap.active {
        .content__items {
            grid-template-columns: 1fr 1fr;
        }
    }
    .main__btn {
        font-size: 18px !important;
    }
    .main__title {
        font-size: 18px !important;
    }
    .content__items {
        gap: 50px;
    }
    .item {
        gap: 20px !important;
        &__name {
            font-size: 14px;
        }
        &__plirce {
            font-size: 14px;
        }
        &__amount {
            input {
                font-size: 14px;
            }
        }
    }
    .slider {
        &__slide {
            a {
                font-size: 16px !important;
            }
        }
        &__prev {
            width: 10px !important;
            height: 20px !important;
            top: 10px !important;
        }
        &__next {
            width: 40px !important;
            height: 40px !important;
        }
    }
}

@media (max-width: 1170px) {
    .content__filter {
        margin-top: 70px !important;
    }
    .filter__blockColor {
        gap: 0!important;
    }
    .filter__price {
        font-size: 14px!important;
    }
    .main__btn {
        font-size: 16px !important;
        gap: 5px !important;
        svg {
            width: 10px !important;
            height: 10px !important;
        }
    }
    .content__items {
        margin-top: 70px;
        grid-template-columns: 1fr 1fr 1fr;
    }
	.slider {
        &__slide {
            a {
                font-size: 14px !important;
            }
        }
    }
}

@media (max-width: 992px) {
    .content__all {
        display: inline-block;
    }
    .filter__blockPrice {
        gap: 25px!important;
    }
    .content__filter {
        margin-top: 50px !important;
    }
    .content__wrap {
        gap: 0px!important;
    }
    .filter__title {
        font-size: 20px !important;
    }
    .fake-radio {
        width: 18px!important;
        height: 18px!important;
    }
    .fake-radio::before {
        width: 32px!important;
        height: 32px!important;
    }
    .filter__block {
        gap: 20px!important;
    }
    .filter__label i {
        bottom: -25px!important;
        font-size: 10px!important;
    }
    .filter__price {
        padding-bottom: 5px!important;
    }
    .sort__block {
        width: 150px !important;
        gap: 25px !important;
    }
    .sort__nav {
        gap: 15px !important;
    }
    .sort__link {
        font-size: 12px !important;
    }
    .main__btn {
        font-size: 13px !important;
    }
    .main {
        margin-top: 20px !important;
        justify-content: center !important;
        gap: 15px !important;
        &__line {
            display: inline-block !important;
            height: 16px;
            width: 1px;
            background:#000;
        }
    }
    .main__title {
        display: none;
    }
    .filter__btn {
        svg {
            display: none;
        }
    }
    .content__items {
        margin-top: 50px;
        gap: 30px;
    }
    .filter {
        display: none;
    }
    .content__wrap.active {
        grid-template-columns: 1fr;
    }
    .item {
        gap: 15px !important;
        &__plus {
            top: -12px !important;
            right: -12px !important;
            width: 30px !important;
            height: 30px !important;
        }
        &__name {
            font-size: 12px !important;
        }
        &__plirce {
            font-size: 12px !important;
        }
        &__amount {
            input {
                font-size: 12px !important;
                width: 20px !important;
                height: 12px !important;
                line-height: 10px;
            }
            button {
                font-size: 15px !important;
            }
        }
        &__status {
            font-size: 9px !important;
        }
        &__info {
            font-size: 9px !important;
        }
    }
	.slider {
        display: none;
        &__prev {
            display: none;
        }
        &__next {
           display: none;
        }
    }
    .content {
        margin-top: 60px;
    }
    .header__mobile {
        margin-bottom: 10px;
        display: grid !important;
    }
    .slider__slide.active a {
        color: #FF6600;
    }
}

@media (max-width: 768px) {
	.header__link a {
        font-size: 12px !important;
    }
    .filter__title {
        font-size: 18px !important;
    }
}

@media (max-width: 576px) {
    .content__filter {
        margin-top: 35px !important;
    }
	.content__items {
        margin-top: 35px;
        grid-template-columns: 1fr 1fr;
    }
}

@media (max-width: 480px) {
    .filter__title {
        font-size: 16px !important;
    }
    .filter__label {
        width: 40px!important;
    }
    .filter__price {
        font-size: 12px!important;
    }
    .sort__block {
        width: 140px !important;
        gap: 20px !important;
        padding: 15px !important;
    }
    .main__btn {
        font-size: 12px !important;
    }
    .header__link a {
        font-size: 10px !important;
    }
    .item {
        gap: 12px !important;
        &__plus {
            top: -9px !important;
            right: -9px !important;
            width: 25px !important;
            height: 25px !important;
        }
        &__name {
            font-size: 10px !important;
        }
        &__plirce {
            font-size: 10px !important;
        }
        &__amount {
            input {
                font-size: 10px !important;
                width: 18px !important;
                height: 12px !important;
                line-height: 10px;
            }
            button {
                font-size: 12px !important;
            }
        }
        &__status {
            font-size: 8px !important;
        }
        &__info {
            font-size: 8px !important;
        }
    }
}