.product {
    background: none;
    .container {
        position: relative;
    }
    &__content {
        margin-top: 40px;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    &__sliderWrap, &__block {
        width: 50%;
    }
    &__block {
        width: 660px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        gap: 27px;
    }
    &__status {
        font-size: 11px;
        color: #FF6600;
    }
    &__title {
        margin: 0;
        padding: 0;
        margin-top: 7px;
        font-weight: 700;
        font-size: 27px;
        text-transform: uppercase;
        color: #171717;
    }
    &__articul {
        margin-top: 7px;
        font-weight: 400;
        font-size: 18px;
        line-height: 18px;
        color: #171717;
    }
    hr {
        margin: 0;
        padding: 0;
        width: 100%;
    }
    &__subtitle {
        margin: 0;
        padding: 0;
        font-weight: 400;
        font-size: 20px;
        color: #171717;
    }
    &__descr {
        margin: 0;
        padding: 0;
        margin-top: 3px;
        font-weight: 300;
        font-size: 17px;
        color: #000000;
    }
    &__about {
        white-space: pre-line;
        margin: 0;
        padding: 0;
        font-weight: 300;
        font-size: 15px;
        color: rgba(0, 0, 0, 0.6);
    }
    &__order {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__price {
        font-weight: 400;
        font-size: 30px;
        color: #FF6600;
    }
    &__amount {
        margin-left: 150px;
        display: flex;
            align-items: center;
            gap: 4px;
            button {
                cursor: pointer;
                font-weight: 400;
                font-size: 24px;
                color: #171717;
                padding: 0;
                margin: 0;
                background: transparent;
                border: none;
                outline: none;
            }
            input {
                padding: 0;
                margin: 0;
                height: auto;
                text-align: center;
                width: 35px;
                font-weight: 400;
                font-size: 20px;
                color: #171717;
                border: none;
                outline: none;
            }
    }
    &__btn {
        cursor: pointer;
        border: none;
        background: #FF6600;
        margin: 0;
        padding: 10px 70px;
        font-weight: 400;
        font-size: 14px;
        text-align: center;
        color: #FFFFFF;
        opacity: 1;
        transition: opacity .2s ease-in-out;
        &:hover {
            opacity: .85;
        }
    }
    &__nav {
        margin-top: 130px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__slide {
        transform:scale(0.9);
        img {
            object-fit: contai;
            width: 100%;
        }
    }
    &__slider {
        margin-right: 0px;
        padding-top: 150px;
    }
    &__slide.swiper-slide-active {
        transform:scale(1.4);
    }
    &__prev {
        z-index: 10;
        cursor: pointer;
        width: 8px;
        height: 18px;
    }
    &__next {
        z-index: 10;
        cursor: pointer;
        width: 32px;
        height: 32px;
    }
    &__pagination {
        max-width: 150px;
        position: static;
        .swiper-pagination-bullet {
            border: 1px solid #656464;
            background: transparent;
        }
        .swiper-pagination-bullet-active {
             border: 1px solid #FF6600;
             background: #FF6600;
        }
    }
}